import React from 'react';
import { Link } from 'react-router-dom';

import './review-preview.styles.scss';
export default function ReviewPreview({ postData }) {
  const { slug, imgUrl, title, clip, date, tags } = postData;

  return (
    <Link to={`/${slug}`} className="link"style={{display: 'inline-block'}}>
      <div className="review-preview">
        <img src={imgUrl} alt="thumbnail" />
        <div className="content">
          <h2 className="title">{title}</h2>
          <div className="clip">{clip}</div>
          <div className="date">{date}</div>
        </div>
      </div>
    </Link>
  );
}
